html, body {
  width: 100%;
  height: 100%;
}

body {
  background: #fff;
}

.lianmeng_header {
  height: .8rem;
  background: url("/StaticResources/public_html/wings/_resources/img/wx/weidulianmeng/header_bian.png") no-repeat center 0.1rem;
  background-size: 98% .6rem;
  position: relative;
}

.lianmeng_header .user_icon {
  width: .52rem;
  height: .52rem;
  position: absolute;
  top: .12rem;
  left: 50%;
  margin-left: -.26rem;
}

.lianmeng_header .user_icon img {
  display: block;
  width: .52rem;
  height: .52rem;
  border-radius: 50%;
}

.lianmeng_header .user_icon .user_name {
  width: .7rem;
  height: .18rem;
  position: absolute;
  left: 0;
  bottom: -.06rem;
  margin-left:-.093rem;
  background:url('/StaticResources/public_html/wings/_resources/img/wx/weidulianmeng/user_namebag.png') no-repeat center;
  background-size:100% .18rem;
}

.lianmeng_header .user_icon .user_name h2 {
  width: 100%;
  height: .18rem;
  line-height:.18rem;
  font-size: .1rem;
  text-align: center;
  color: #fff;
}

.lianmeng_header .tongji_form {
  width: .7rem;
  height: .16rem;
  position: absolute;
  left: .15rem;
  bottom: 0;
}

.lianmeng_header .tongji_form img {
  display: block;
  width: .7rem;
  height: .16rem;
}

.lianmeng_header .time_yes {
  position: absolute;
  right: .15rem;
  bottom: 0;
  color: #999;
  font-size: .1rem;
  font-weight: bold;
}

.lianmeng_header .time_yes b {
  font-weight: 550;
}

.tongji_shuliang {
  width: 100%;
  height: .4rem;
  margin-bottom: .2rem;
}

.tongji_shuliang .tongji_list {
  height: .3rem;
  margin-top: .35rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}

.tongji_shuliang .tongji_list li {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  width: .1px;
  height: .3rem;
  font-weight: bold;
  text-align: center;
  background: url("/StaticResources/public_html/wings/_resources/img/wx/weidulianmeng/right_tiao.png") no-repeat right center;
}

.tongji_shuliang .tongji_list li p {
  color: #b4b4b4;
  font-size:.12rem;
}

.tongji_shuliang .tongji_list li span {
  color: #39d;
  font-size:.14rem;
  display: block;
  margin-top:.08rem;
}

.zhanghao_core {
  width: 100%;
  height: .4rem;
  background: #f8f8f8;
}

.zhanghao_core.xiding {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  height: .2rem;
  padding-bottom: .2rem;
}

.zhanghao_core .zhanghao_cores {
  display: -webkit-box;
  display: -webkit-flex;
  padding:0 .15rem;
  display: flex;
  height: .4rem;
  font-size: .12rem;
  color: #999;
  font-weight: bold;
  line-height: .4rem;
}

.zhanghao_core .zhanghao_cores li:nth-child(1) {
  -webkit-box-flex: 2;
  -webkit-flex: 2;
  flex: 2;
  width: .1px;
  padding-left: .1rem;
}

.zhanghao_core .zhanghao_cores li:nth-child(2) {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  width: .1px;
  text-align: center;
  font-weight:bold;
  font-size: .1rem;
}

.zhanghao_core .zhanghao_cores li:nth-child(3) {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  width: .1px;
  text-align: center;
}

.zhanghao_list {
  padding-left: .15rem;
}

.zhanghao_list .zhanghao_name1 {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  height: .75rem;
  margin-top: .14rem;
  padding-right: .15rem;
  border-bottom: 1px solid #bbb;
}

.zhanghao_list .zhanghao_name1 li:nth-child(1) {
  -webkit-box-flex: 2;
  -webkit-flex: 2;
  flex: 2;
  width: .1px;
}

.zhanghao_list .zhanghao_name1 li:nth-child(1) .user_detail {
  width: 100%;
  height: .75rem;
  overflow: hidden;
}

.zhanghao_list .zhanghao_name1 li:nth-child(1) .user_detail .user_pic1 {
  margin-top: .12rem;
  float: left;
  width: .5rem;
  height: .64rem;
}

.zhanghao_list .zhanghao_name1 li:nth-child(1) .user_detail .user_pic1 img {
  display: block;
  width: .4rem;
  height: .4rem;
  border-radius: 50%;
}

.zhanghao_list .zhanghao_name1 li:nth-child(1) .user_detail .user_what {
  float: left;
  overflow:hidden;
}

.zhanghao_list .zhanghao_name1 li:nth-child(1) .user_detail .user_what .user-id {
  height: .2rem;
  line-height: .2rem;
  overflow:hidden;
}

.zhanghao_list .zhanghao_name1 li:nth-child(1) .user_detail .user_what .user-id b {
  float:left;
  font-size: .1rem;
  color: #999;
  font-weight: bold;
}

.zhanghao_list .zhanghao_name1 li:nth-child(1) .user_detail .user_what .user-id span {
  float:left;
  margin-left:.04rem;
  font-size: .1rem;
  color: #39d;
  font-weight: bold;
}

.zhanghao_list .zhanghao_name1 li:nth-child(1) .user_detail .user_what .user-id p {
  font-size: .1rem;
  color: #999;
  font-weight: bold;
}

.zhanghao_list .zhanghao_name1 li:nth-child(1) .user_detail .user_what h2 {
  height: .22rem;
  line-height: .22rem;
  font-size: .12rem;
  color:#333;
}

.zhanghao_list .zhanghao_name1 li:nth-child(1) .user_detail .user_what p {
  display: block;
  height: .18rem;
  line-height: .18rem;
}

.zhanghao_list .zhanghao_name1 li:nth-child(2) {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  width: .1px;
  text-align: center;
  line-height: .55rem;
  color: #ff9600;
  font-weight:bold;
  font-size: .1rem;
}

.zhanghao_list .zhanghao_name1 li:nth-child(3) {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  width: .1px;
  text-align: center;
  line-height: .55rem;
  font-size: .1rem;
  color: #999;
  font-weight: bold;
}